  background: transparent;
  color: #FFFFFF;
  border: 1px solid currentColor;
  box-shadow: none;
}

.st_default_btn.st_transparent:hover {
  color: #FFFFFF !important;
  box-shadow: none;
}

.st_default_btn.st_plain_text {
  background: transparent;
  color: #FFFFFF;
  border: none;
  box-shadow: none;
  text-shadow: none;
}

.st_default_btn.st_plain_text:hover {
  box-shadow: none;
}

.st_default_btn.st_icon_btn {
  background: transparent;
  color: #FFFFFF;
  border: none;
  box-shadow: none;
  text-shadow: none;
  padding: 0;
}

